import {formatDate} from '../../common/js/commonUtil.js'
import {api} from '../../api/request.js'
import md5 from '../../common/js/md5.js'
import {mapMutations} from 'vuex';

export default {
	data() {
		return {
			phone: '',
			code: '',
			logining: false,
			codetext: '获取验证码',
			codenum: 60,
			disabled: false,
			appid: "App001",
			countrycode: "86",
		}
	},
	onLoad() {},
	methods: {

		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},
		toRegist() {
			uni.navigateTo({
				url: "register"
			})
		},
		// 60S 倒计时
		timer() {
			let that = this;
			if (this.codenum == 0) {
				this.codetext = "获取验证码"
				this.codenum = 60
				this.disabled = false
				return false
			} else {
				this.disabled = true
				this.codetext = this.codenum + "秒后重新发送"
				this.codenum--
			}
			setTimeout(() => {
				that.timer()
			}, 1000);
		},
		// 发送验证码
		async sendcode() {
			if (this.phone == '') {
				this.$api.msg('手机号码不能为空');
				return
			}
			this.timer()
			let sendtime = formatDate(new Date(), "yyyyMMddhhmmss");
			let token = md5(this.phone + this.appid + "abc123!!" + sendtime);
			uni.request({
				url: api + '/api/WeiXinSMS/SendCode',
				method: "POST",
				data: {
					phoneNum: this.phone,
					appID: this.appid,
					sendTime: sendtime,
					countrycode: this.countrycode,
					token: token
				},
				success: (res) => {
					console.log(res);
				}
			});
		},
		// 验证码登录
		async toLogin() {
			let that = this
			let sendtime = formatDate(new Date(), "yyyyMMddhhmmss");
			let token = md5(this.phone + this.code + this.appid + "abc123!!" + sendtime);
			uni.request({
				url: api + "/api/Account/LoginByCode",
				method: "POST",
				data: {
					phoneNum: this.phone,
					verifyCode: this.code,
					appID: this.appid,
					sendTime: sendtime,
					token: token
				},
				success(res) {
					if (res.data.statusCode == 1) {
						console.log(res.data.data)
						that.$store.commit('login', res.data.data)
						uni.switchTab({
							url: "../index/index"
						})
					}
					if (res.data.statusCode == 2) {
						uni.showModal({
							content: res.data.msg,
							showCancel: false
						});
					}
				}
			})
		}
	},

}
